@import url("https://fonts.googleapis.com/css2?family=Khand:wght@300;400;500;600;700&family=Roboto:ital,wght@0,300;0,700;0,900;1,300;1,700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none;
  cursor: pointer; }

ul,
ol,
dd {
  list-style-type: none;
  margin: 0;
  padding: 0; }

:root {
  /* Primary colors */
  --color-primary: hsl(202, 54%, 40%);
  --color-primary-hs: 202, 54%;
  /* base color */
  --color-primary-l: 40%;
  /* base lightness*/
  --color-primary-dark: hsl(var(--color-primary-hs),calc(var(--color-primary-l) - 15%));
  --color-primary-dark-m: hsl(var(--color-primary-hs),calc(var(--color-primary-l) - 20%));
  --color-primary-dark-x: hsl(var(--color-primary-hs),calc(var(--color-primary-l) - 25%));
  --color-primary-light: hsl(var(--color-primary-hs),calc(var(--color-primary-l) + 15%));
  --color-primary-light-m: hsl(var(--color-primary-hs),calc(var(--color-primary-l) + 30%));
  --color-primary-light-x: hsl(var(--color-primary-hs),calc(var(--color-primary-l) + 50%));
  /* Secondary colors */
  --color-secondary: hsl(26, 90%, 54%);
  --color-secondary-hs: 26, 90%;
  /* base color */
  --color-secondary-l: 54%;
  /* base lightness*/
  --color-secondary-dark: hsl(var(--color-secondary-hs),calc(var(--color-secondary-l) - 15%));
  --color-secondary-dark-m: hsl(var(--color-secondary-hs),calc(var(--color-secondary-l) - 20%));
  --color-secondary-dark-x: hsl(var(--color-secondary-hs),calc(var(--color-secondary-l) - 25%));
  --color-secondary-light: hsl(var(--color-secondary-hs),calc(var(--color-secondary-l) + 15%));
  --color-secondary-light-m: hsl(var(--color-secondary-hs),calc(var(--color-secondary-l) + 30%));
  --color-secondary-light-x: hsl(var(--color-secondary-hs),calc(var(--color-secondary-l) + 40%));
  --color-text: --black;
  --color-text-hover: var(--color-secondary);
  --black: #1d1d21;
  --gray-10: #2e2e31;
  --gray-6: #7b7a7d;
  --gray-4: #a5a5a6;
  --gray-3: #bbbbbc;
  --gray-2: #d1d0d2;
  --gray-1: #e8e7e8;
  --white: white;
  /* feedback */
  --color-success: #88c459;
  --color-error: #f5414f;
  --color-warning: #ffd137; }

:root {
  --content-max-width: 1400px; }

body > * {
  max-width: calc(100% - 30px);
  margin: 0 auto; }

.g-1 {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 1rem; }

.g-1 > * {
  background-color: var(--color-primary-light-x);
  padding: 1.5rem;
  border-radius: .5rem; }

.g-2 {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 1rem; }

.g-2 > * {
  background-color: var(--color-primary-light-x);
  padding: 1.5rem;
  border-radius: .5rem; }

.colors-primary, .colors-secondary {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 1rem;
  padding-bottom: 1rem; }
  .colors-primary > div, .colors-secondary > div {
    background-color: #eee;
    padding: 1.5rem;
    border-radius: .5rem; }
    .colors-primary > div:nth-child(1), .colors-secondary > div:nth-child(1) {
      grid-column: span 3;
      background-color: var(--color-primary);
      color: white; }
    .colors-primary > div:nth-child(2), .colors-secondary > div:nth-child(2) {
      color: white;
      background-color: var(--color-primary-dark); }
    .colors-primary > div:nth-child(3), .colors-secondary > div:nth-child(3) {
      color: white;
      background-color: var(--color-primary-dark-m); }
    .colors-primary > div:nth-child(4), .colors-secondary > div:nth-child(4) {
      color: white;
      background-color: var(--color-primary-dark-x); }
    .colors-primary > div:nth-child(5), .colors-secondary > div:nth-child(5) {
      background-color: var(--color-primary-light); }
    .colors-primary > div:nth-child(6), .colors-secondary > div:nth-child(6) {
      background-color: var(--color-primary-light-m); }
    .colors-primary > div:nth-child(7), .colors-secondary > div:nth-child(7) {
      background-color: var(--color-primary-light-x); }

.colors-secondary > div:nth-child(1) {
  grid-column: span 3;
  background-color: var(--color-secondary);
  color: white; }

.colors-secondary > div:nth-child(2) {
  color: white;
  background-color: var(--color-secondary-dark); }

.colors-secondary > div:nth-child(3) {
  color: white;
  background-color: var(--color-secondary-dark-m); }

.colors-secondary > div:nth-child(4) {
  color: white;
  background-color: var(--color-secondary-dark-x); }

.colors-secondary > div:nth-child(5) {
  background-color: var(--color-secondary-light); }

.colors-secondary > div:nth-child(6) {
  background-color: var(--color-secondary-light-m); }

.colors-secondary > div:nth-child(7) {
  background-color: var(--color-secondary-light-x); }

.c1, .c2, .c3, .c4, .c5, .c6, .c7, .c8, .c9, .c10 {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  max-width: 100%;
  box-sizing: border-box; }
  .c1 > *, .c2 > *, .c3 > *, .c4 > *, .c5 > *, .c6 > *, .c7 > *, .c8 > *, .c9 > *, .c10 > * {
    flex-basis: 100%;
    flex: 1 1 auto; }

.c2 > * {
  flex-basis: 50%; }

.c3 > * {
  flex-basis: calc(100% / 3); }

.c4 > * {
  flex-basis: 25%; }

.c5 > * {
  flex-basis: 20%; }

.c6 > * {
  flex-basis: calc(100% / 6); }

.flex-100 {
  flex-basis: 100%; }

.flex-50 {
  flex-basis: 50%; }

.flex-h-center {
  justify-content: center; }

.flex-v-center {
  align-items: center; }

.center {
  text-align: center; }

.content-width {
  padding-left: calc((100% - var(--content-max-width)) / 2);
  padding-right: calc((100% - var(--content-max-width)) / 2); }

@media only screen and (min-width: 1024px) {
  body > * {
    max-width: 1400px; }
  .c1, .c2, .c3, .c4, .c5, .c6, .c7, .c8, .c9, .c10 {
    flex-flow: row; } }

.flex-wrap {
  flex-wrap: wrap; }

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: var(--black); }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Khand", sans-serif;
  font-weight: 600;
  line-height: 1.2;
  margin: 5px 0; }

h1,
h2 {
  margin: 10px 0; }

h1 {
  font-size: 30px; }

h2 {
  font-size: 24px; }

h3 {
  font-size: 18px; }

h4 {
  font-size: 14px; }

h5 {
  font-size: 12px; }

h6 {
  font-size: 10px; }

p {
  margin: 5px 0; }

a {
  color: var(--color-text); }
  a:hover {
    color: var(--color-text-hover); }

body,
html {
  padding: 0;
  margin: 0; }

nav.main {
  background: var(--color-primary);
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 40px;
  padding: 0 15px;
  max-width: 100%; }
  nav.main a {
    color: var(--white);
    padding: 0 5px; }
    nav.main a:hover {
      color: var(--color-secondary); }
  nav.main .logo {
    display: block;
    align-items: center; }
    nav.main .logo img {
      height: 27px;
      transition: transform .3s; }
    nav.main .logo:hover img {
      transform: scale(1.05); }
  nav.main ul.menu {
    display: flex; }

@media screen and (max-width: 1024px) {
  .hide-mobile {
    display: none !important; }
  .hide-desktop {
    display: inherit; } }

@media screen and (min-width: 1025px) {
  .hide-mobile {
    display: inherit; }
  .hide-desktop {
    display: none !important; } }

.wrap {
  flex-wrap: wrap; }
